.HeadBar{
  display: flex;
  justify-content: space-between;
  padding: 20px 80px;
  gap:50px;
  align-items: center;
}

.HeadBar > div{
  display: flex;
  align-items: center;
  gap:20px;
}

.HeadBar img{
  width: 50px;
  height: auto;
}

.HeadBar > div:last-child{
  font-weight: 500;
  font-size: 1.3em;
}

.HeadBar > div:last-child > p:first-child{
  color: #32b860;

}

.HeadBar > div:last-child > p:last-child{
  color: #d9ba4a;
}
